import { defineStore } from "pinia";
import axios from "axios";
import { config } from "./utils/config";
import { telegramStore } from "@/store/telegram";
import { checkResponseSuccess } from "@/store/utils/apiUtils";

interface ReferalData {
  referalLink: string,
  preparedInlineMessageID: number
}

export const friendsStore = defineStore("friends", {
  state: () => ({
    referalData: null as null | ReferalData,
    friends: [
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
    ],
  }),

  actions: {
    async fetchFriends() {
      try {
        const webAppData = telegramStore().getWebAppData;

        const response = await axios.post(
          `${config.backendURL}/api/cards/get-karma`,
          webAppData
        );

        checkResponseSuccess(response);

        this.friends = response.data.data.friends;
      } catch (error) {
        console.error("Ошибка при получении друзей:", error);
        throw new Error("Server error when getting friends list");
      }
    },

    async ensureReferalData() {
      if (!this.referalData) {
        try {
          this.referalData = await this.requestReferalData()
        } catch (error) {
          console.error("Ошибка получения реферальных данных:", error)
          throw new Error("Error when getting referal data")
        }
      }
      return this.referalData
    },

    async requestReferalData() {
      const webAppData = telegramStore().getWebAppData;

      const response = await axios.post(
          `${config.backendURL}/api/request-invite-link`,
          webAppData
      );

      checkResponseSuccess(response);

      return {
        referalLink: response.data.data.referalLink,
        preparedInlineMessageID: response.data.data.preparedInlineMessageID
      };
    },
  },

  getters: {},
});
